<template>
	<div class="btn-box">
		<div class="left-bnt" v-for="(item,index) in btnArr" :key="index" @click="itemClick(index)">
			<span class="btn" :class="{btnactive:currentIndex==index}">{{item}}</span>
		</div>
	</div>
</template>

<script>



export default {
  name: 'NavItemBtn',
   data(){
	return{
		currentIndex:-1
	}
   },
   props:{
		btnArr:{
			type:Array,
			default(){
				return []
			}
		}
   },
   methods:{
	//这个是输出给上一个组件点击了哪个按钮
	itemClick(index){
		if(this.currentIndex==index){
			this.currentIndex=-1
		}else{
			this.currentIndex=index
		}
		//把事件传给navitem页面
		this.$emit("btnClick",this.currentIndex)
	},
	nameOfEvent(){
		this.currentIndex=-1
	}
   },
	created() {
		var self = this
		this.$bus.$on('cancelCurrentIndex', function() {
			self.nameOfEvent()
		})
	},
}
</script>


<style>

.btn-box{
	overflow:hidden;
}
.btn-box div{
	text-align:center;
	float:left;
	margin-top:8px;
}

.btn{
	display:inline-block;
	width:95%;
	background:#eaeaea;
	color:black;
	line-height:30px;
	border:1px solid #eaeaea;
	border-radius:5px;
	font-size:14px;
}
.btnactive{
	border:1px solid #fb6b6b;
	color:#ff4545;
	background:#fff;
}

  
</style>
