<template>
	<div id="filter-page">
		<!--顶部菜单-->
		<nav-bar class="home-nav-bar">
			<div slot="center">
				{{title}}{{message}}
			</div>
		</nav-bar>
		<nav-content/>
		<bottom-btn class="bottom-btn">
			<div slot="reset"  class="reset-btn" @click="resetClick">重置</div>
			<div slot="success"  class="success-btn" @click="successClick">完成</div>
		</bottom-btn>
	</div>
</template>

<script>

import NavBar from 'components/common/navbar/NavBar'
import BottomBtn from 'components/common/bottombtn/BottomBtn'

import NavContent from './childComps/NavContent'




export default {
  name: 'FilterPage',
	components:{
		NavBar,
		NavContent,
		BottomBtn
	},
	data(){
		return{
			title:'筛选',
			message:''
		}
	},
	methods:{
		resetClick(){
			console.log("cz")
			//使用事件总线
			this.$bus.$emit('cancelCurrentIndex')
		},
		successClick(){
			//完成-跳转页面并且传一个数据进去-
			console.log("wc")
			//this.$router.push("/allgoods").catch(err=>{err})
			this.$router.push({ name: 'allgoods', params: { test:this.$store.state.filters}})
			
		},
		nameOfEvent(value) {
			this.message = value
		}
	},
	created() {
		var self = this
		this.$bus.$on('nameOfEvent', function(value) {
			self.nameOfEvent(value)
		})
	}
}
</script>


