<template>
	<div class="nav-item">
		<!--这里包含的是操作系统、区服、公示期的组件-->
		<h3 class="title">{{btnArr.title}}<span class="serve-type">{{currentText}}</span></h3>
		<slot name="textInput"></slot>
		<nav-item-btn @btnClick="btnClick" :btn-arr="btnArrs" ref="navitem"/>
	</div>
</template>

<script>

import NavItemBtn from './NavItemBtn'

export default {
  name: 'NavContent',
	components:{
		NavItemBtn
	},
	methods:{
		//这里是上一个组件传递过来选择了哪一个按钮
		btnClick(index){
			this.currentText=this.btnArr.arr[index]
			//是否已经存在该值
			//let old=this.$store.state.filters[this.btnArr.type].find(item=>item.type==this.currentText)
		
			//console.log(old)
			//写入vuex里
			//把选择好的储存起来-这里最好还是写回vuex的actions里面
			this.$store.state.filters[this.btnArr.type]=this.currentText
			console.log(this.$store.state.filters[this.btnArr.type])
		},
		//点了重置按钮清空文本
		nameOfEvent(){
			this.currentText=""
			this.$store.state.filters[this.btnArr.type]=this.currentText
		}
	},
	props:{
		//父组件传过来的对象
		btnArr:{
			type:Object,
			default(){
				return{}
			}
		}
	},
	mounted(){
		//是否有选中的类别
		this.$refs.navitem.currentIndex=this.btnArr.active
	}
	,
	data(){
		return{
			//当前选择的文本
			currentText:'',
		}
	},
	//计算属性
	computed:{
		btnArrs(){
			return this.btnArr.arr
		}
	},
	created() {
		//这是filterPage页面传过来的事件
		var self = this
		this.$bus.$on('cancelCurrentIndex', function() {
			self.nameOfEvent()
		})
	},
}
</script>


<style>

.serve-type{
	font-size:14px;
	font-weight:normal;
	float:right;
	color:red;
}
.title{
	font-size:15px;
	color:black;
	padding:8px 0;
}
.nav-item{
	padding:12px 10px;
}

  
</style>
